	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Don't miss an upload! Subscribe! http://goo.gl/szEauh\n\nHow does the pervasiveness of JavaScript on the client change how we architect applications? We can create hundreds virtual machines in the cloud, but we are using the millions of visual machines that visit our sites every day?\n\nSuddenly we are scripting against thousands of Virtual Machines from the command line while creating things today with JavaScript in the browser that were impossible yesterday. LiveScript becomes JavaScript becomes ES6 and now we're compiling C++ to JavaScript.\n\nJoin Scott Hanselman as he explores the relationship between the Cloud and the Browser, many Languages and one Language, how it might all fit together and what comes next.\n\nBuy Scott's books here. http://goo.gl/Y2Zsdh\n\nStay Connected to O'Reilly Media by Email - http://goo.gl/YZSWbO\n\nFollow O'Reilly Media:\nhttp://plus.google.com/+oreillymedia\nhttps://www.facebook.com/OReilly\nhttps://twitter.com/OReillyMedia","meta":{"duration":"PT25M57S"},"presenter":{"name":null},"source":{"id":"UzyoT4DziQ4","provider":"youtube"},"slug":"scott-hanselman-virtual-machines-javascript-and-assembler-fluent-2014-keynote","tag":["JavaScript","Assembly Language (Programming Language)","fluent 2014","fluentconf","fluent conference","Virtual Machine (Programming Language Paradigm)","scott hanselman","keynote","speaker","podcaster","Programming Language (Literary Genre)","o'reilly books","O'Reilly Media (Business Operation)","oreilly","oreilly media","O'Reilly"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/UzyoT4DziQ4/default.jpg","height":90,"width":120}},"title":"Scott Hanselman, \"Virtual Machines, JavaScript and Assembler\" -  Fluent 2014 Keynote"}}]}},"pathContext":{"slug":"podcaster","tag":"podcaster"}}

